<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`ImportLcExpired${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import ImportLcExpiredListLayout from '@/views/trade/import-lc/import-lc-expired/import-lc-expired-browse/ImportLcExpiredListLayout'
import ImportLcExpiredCardLayout from '@/views/trade/import-lc/import-lc-expired/import-lc-expired-browse/ImportLcExpiredCardLayout'
import ImportLcExpiredPivotLayout from '@/views/trade/import-lc/import-lc-expired/import-lc-expired-browse/ImportLcExpiredPivotLayout'
import ImportLcExpiredTableMixin from '@/mixins/trade/import-lc/ImportLcExpiredTableMixin'

export default {
  name: 'import-lc-expired-browse',
  mixins: [ImportLcExpiredTableMixin],
  components: {
    ImportLcExpiredListLayout,
    ImportLcExpiredCardLayout,
    ImportLcExpiredPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadImportLcTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
